.parent {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap
}

.children {
    display: flex;
    flex-direction: column;
}

.title {
    font-size: 15px;
    width: 100%;
    max-width: 150px;
    margin: 0
}

.fixing {
    display: flex;
    flex-direction: column;
    border: 3px solid #808080;
    margin-left: 30px;
    width: 220px;
    height: 35vh;
    margin-bottom: 5px;

}

.image {
    margin: 0 auto;
    width: 80%
}

.btns {
    background: none;
    border: none;
    cursor: pointer;
}

.btnsParent {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.search {
    width: 20%;
    margin: 25px 0 0 25px;
}

.fixingPositions {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
}

.addingBtn {
    background-color: #f5ece7;
    font-size: 12px;
    cursor: pointer;
    width: 15%;
    margin: 25px 25px 0 0;
}

.picTitleParent {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.titleParent {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 25px 5px 0 5px;
}

.price {
    margin: 0
}